<template>
  <nav>
    
  </nav>
  <router-view />
</template>

<script>
import axios from "axios";
import { mapActions, mapGetters } from "vuex";
export default {
  name: 'App',
  async created() {
    this.GET_ADDRESS({ limit: 100 });
    this.GET_CATEGORY({limit: 20});
    try{
      if(localStorage.getItem("token")) {
        this.SET_SESSION_ADMIN(localStorage.getItem("token"));
        await axios.post(process.env.VUE_APP_SERVER_URL + "/api/admin/user/check");
      }
    } catch(err) {
      console.log(err);
      this.SET_SESSION_ADMIN("");
    }
    try{
      if(localStorage.getItem("cart")) this.UPDATE_CART(JSON.parse(localStorage.getItem("cart")));
    } catch(err) {
      console.log(err);
    }
    this.$watch(
      () => this.$route,
      () => {
        if(this.$route.path.startsWith("/admin") && !this.$route.path.startsWith("/admin/login") && !this.SESSION)
          this.$router.push({ path: "/admin/login" });
      },
      // fetch the data when the view is created and the data is
      // already being observed
      { immediate: true }
    )
  },
  computed: {
    ...mapGetters(["SESSION"]),
  },
  methods: {
    ...mapActions(["SET_SESSION_ADMIN", "GET_ADDRESS", "GET_CATEGORY", "UPDATE_CART"]),
  },
};
</script>

