<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "HeaderComponent",
  props: {
    color: String,
  },
  data() {
    return {
      activeMenu: false,
      activeSearch: false,
      adaptiveNav: false,
      activeShadow: false,
      search: "",
      SERVER_URL: process.env.VUE_APP_SERVER_URL,
      bonusUri: process.env.NODE_ENV == "production" ? "/static/" : "/",
    };
  },
  mounted() {
    document.body.style.overflow = "inherit";
  },
  computed: {
    ...mapGetters(["TABLE_CATEGORY", "S_PRODUCTS", "CART"]),
  },
  methods: {
    change() {
      console.log(this.search);
      this.SEARCH_PRODUCTS({ search: this.search, limit: 4 });
    },
    activateBurgerMenu() {
      this.activeMenu = true;
      this.activeShadow = true;
      if (this.activeMenu === true) {
        document.body.style.overflow = "hidden";
      }
    },
    deactiveBurgerMenu() {
      this.activeMenu = false;
      this.activeShadow = false;
      document.body.style.overflow = "inherit";
    },
    activeSearchMenu() {
      this.activeSearch = true;
      this.adaptiveNav = true;

      if (this.activeSearch === true) {
        document.body.style.overflow = "hidden";
      }
    },
    deactiveSearchMenu() {
      this.activeSearch = false;
      this.adaptiveNav = false;
      document.body.style.overflow = "inherit";
    },
    ...mapActions(["SEARCH_PRODUCTS"]),
  },
};
</script>

<style>
  ul {
    list-style: none;
    padding: 0;
  }

  li {
    color: #ffffff;
    font-size: 17px;
  }

  .navbar-container {
    display: grid;
    grid-template-columns: 9fr 9fr 1fr;
    align-items: center;
    padding-top: 20px;
    position: relative;
    z-index: 100;
  }

  .main-header_burger {
    display: none;
  }
  .main-header_info {

    margin-right: 70px;
  }
  .main-header_info ul {
    display: flex;
    justify-content: space-between;
    width: 100%;

  }

  .main-header_search ul {
    display: flex;
    gap: 20px;
  }

  .main-header_search-panel.active_search {
    transform: translateY(0);
    transition: all 0.5s;
    z-index: 90;
  }

  .main-header_info.active_search li {
    color: black;
  }

  .main-header_search.active_search svg path {
    fill: black;
  }

  .main-header_search-panel {
    height: 90%;
    width: 100%;
    background: #fff;
    position: fixed;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-around;
    transform: translateY(-400%);
    transition: all 1s;
    z-index: 0;
  }

  .wrapper-close-icon {
    display: none;
  }

  .search-wrapper {
    display: flex;
    max-width: 1450px;
    width: 90%;
    padding-top: 100px;
  }

  .wrapper-title,
  .wrapper-links {
    color: black;
  }

  .wrapper-title h2 {
    font-size: 20px;
  }

  .search-wrapper input {
    max-width: 1450px;
    width: 100%;
  }

  .search-wrapper button {
    padding: 7px 55px;
    background: black;
    color: #fff;
  }

  .wrapper-links {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 700px;
    width: 100%;
  }

  .main-header_adaptive-menu {
    display: none;
  }

  .main-header_adaptive-menu__shadow {
    display: none;
  }

  .search-item img {
    width: 166px;
    height: 300px;
    object-fit: cover;
  }

  .wrapper-products {
    display: flex;
  }

  .cart-num {
    position: absolute;
    right: -7px;
    bottom: -13px;
    color: white;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: black;
    text-align: center;
    font-size: 15.5px;
  }
  @media (min-width: 900px) and (max-width: 1024px) {
    .main-header_info {
      margin-right: 30px;
    }
  }
  @media (max-width: 900px) {

    .wrapper-products {
      flex-direction: column;
      justify-content: flex-start !important;
    }

    .search-item {
      align-items: center;
    }

    .main-header_info {
      display: none;
    }

    .main-header_burger {
      display: block;
      width: 45px;
      height: 30px;
    }

    .main-header_burger.active_search svg rect {
      fill: black;
    }

    .main-header_adaptive-menu.active {
      transform: translateX(0);
      transition: all 0.5s;
    }

    .main-header_adaptive-menu {
      background: #fff;
      transform: translateX(-200%);
      transition: all 0.5s;
      position: fixed;
      top: 0;
      left: 0;
      height: 100vh;
      width: 85%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      padding: 25px 0;
      z-index: 0;
    }

    .main-header_adaptive-menu__shadow.active_shadow {
      background: black;
      display: block;
      height: 100vh;
      width: 100%;
      opacity: 60%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      position: absolute;
    }

    .main-header_adaptive-menu__info ul {
      display: flex;
      flex-direction: column;
      text-align: center;
      gap: 20px;
    }

    .main-header_adaptive-menu li {
      color: black;
    }

    .main-header_adaptive-menu__socials {
      display: flex;
      gap: 25px;
    }

    .navbar-container {
      display: grid;
      grid-template-columns: 10fr 10fr 1fr;
      padding-left: 20px;
