import { createRouter, createWebHistory } from "vue-router";
import HomeAdminPage from "@/views/admin/HomeAdminPage";
import ProductsAdminPage from "@/views/admin/ProductsAdminPage";
import TableCollectionAdminPage from "@/views/admin/TableCollectionAdminPage";
import CategoryAdminPage from "@/views/admin/CategoryAdminPage";
import FormCollcetionAdminPage from "@/views/admin/FormCollectionAdminPage";
import MainPage from "@/views/client/MainPage";
import CollectionPage from '@/views/client/CollectionPage';
import ProductsPage from '@/views/client/ProductsPage';
import CatalogPage from '@/views/client/CatalogPage';
import AboutProductPage from '@/views/client/AboutProductPage';
import ShopAddress from '@/views/client/ShopAddress' ;
import TableProductsAdminPage from '@/views/admin/TableProductsAdminPage';
import TablePurchasesAdminPage from '@/views/admin/TablePurchasesAdminPage';
import LoginAdminPage from '@/views/admin/LoginAdminPage';
import ChangePlaceAdminPage from '@/views/admin/ChangePlaceAdminPage';
import CartPage from '@/views/client/CartPage';
import AboutBrand from '@/views/client/AboutBrand';

const routes = [
  {
    path: "/admin",
    name: "admin-home",
    component: HomeAdminPage,
  },
  {
    path: "/",
    name: "home",
    component: MainPage,
  },
  {
    path: "/collection/:id",
    name: "collection-page",
    component: CollectionPage,
  },
  {
    path: "/catalog-page",
    name: "catalog-page",
    component: CatalogPage,
  },
  {
    path: "/products/:collection",
    name: "products",
    component: ProductsPage,
  },
  {
    path: "/about_product/:id",
    name: "about-product",
    component: AboutProductPage,
  },
  {
    path: "/shop_address",
    name: "shop-address",
    component: ShopAddress,
  },
  {
    path: "/about_brand",
    name: "about-brand",
    component: AboutBrand,
  },
  {
    path: "/admin/add_products/:id?",
    name: "admin-add-products",
    component: ProductsAdminPage,
  },
  {
    path: "/admin/collection",
    name: "admin-collection",
    component: TableCollectionAdminPage,
  },
  {
    path: "/admin/category",
    name: "admin-category",
    component: CategoryAdminPage,
  },
  {
    path: "/admin/add_collection",
    name: "admin-add_collection",
    component: FormCollcetionAdminPage,
  },
  {
    path: "/admin/table_products",
    name: "table-products",
    component: TableProductsAdminPage,
  },
  {
    path: "/admin/table_purchases",
    name: "table-purchases",
    component: TablePurchasesAdminPage,
  },
  {
    path: "/admin/login",
    name: "login-admin",
    component: LoginAdminPage,
  },
  {
    path: "/admin/change-place",
    name: "change-place-admin",
    component: ChangePlaceAdminPage,
  },
  {
    path: "/client/cart-page",
    name: "cart-page",
    component: CartPage,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
