<template>
  <HeaderComponent :color="'black'" />
  <div class="product container">
    <div class="product-img">
      <img :src="SERVER_URL + bonusUri + PRODUCT.img" alt="" />
    </div>
    <div class="product-description">
      <h3>{{ PRODUCT.title }}</h3>
      <p class="price">{{ PRODUCT.priceAndVolume[selectedVolume].price }} р.</p>
      <p>
        {{ PRODUCT.description }}
      </p>
      <div class="product-description_smallvalue">
        <button v-for="(item, index) of PRODUCT.priceAndVolume" :selected="selectedVolume == index" :key="index" @click="selectVolume(index)">
          <img src="@/assets/kibil_product.png" alt="" />
          <p>{{ item.volume }}мл</p>
        </button>
      </div>
      <button class="submit" @click="ADD_CART_PRODUCT({ ...PRODUCT, volume: selectedVolume, count: 1 })/* activeModalWindow */">Добавить в корзину</button>
    </div>
  </div>
  <FooterComponent />
</template>

<script>
import HeaderComponent from "@/components/client/HeaderComponent.vue";
import FooterComponent from "@/components/client/FooterComponent.vue";
import { mapActions, mapGetters } from "vuex";
/* import axios from "axios"; */
export default {
  name: "AboutProductPage",
  components: { HeaderComponent, FooterComponent },
  data() {
    return {
      SERVER_URL: process.env.VUE_APP_SERVER_URL,
      mode: process.env.NODE_ENV,
      bonusUri: process.env.NODE_ENV == "production" ? "/static/" : "/",
      active: false,
      activeWindow: false,
      selectedVolume: 0,
      form: {
        name: "",
        phone_number: 0,
        email: "",
        quantity_of_goods: 1,
        city: "",
        volume: 0
      }
    };
  },
  computed: {
    ...mapGetters(["PRODUCT"]),
  },
  async created(){
    await this.GET_PRODUCT_BY_ID(this.$route.params.id);
    this.form.volume = this.PRODUCT.priceAndVolume[this.selectedVolume].volume;
  },
  methods: {
    /* activeModalWindow() {
      this.active = true;
      this.activeWindow = true;
      document.body.style.overflow = "hidden";
    },
    deactiveModalWindow() {
      this.active = false;
      this.activeWindow = false;
      document.body.style.overflow = "inherit";
    },
    plusBtn() {
      this.form.quantity_of_goods++;
    },
    minusBtn() {
      if (this.form.quantity_of_goods > 1) {
        this.form.quantity_of_goods--;
      } else {
        return;
      }
    },
    submit(event) {
      event.preventDefault();
      axios({
        method: "POST",
        url: process.env.VUE_APP_SERVER_URL + "/api/admin/user_client",
        data: {...this.form, productId: Number(this.$route.params.id)}
      }).then((res) => {
        console.log(res);
      }).catch((err) => {
        console.log(err);
      });
    }, */
    selectVolume(volume) {
      this.selectedVolume = volume;
      this.form.volume = this.PRODUCT.priceAndVolume[this.selectedVolume].volume
    },
    ...mapActions(["GET_PRODUCT_BY_ID", "ADD_CART_PRODUCT"]),
  },
};
</script>

<style scoped>
button {
  border: none;
  background: transparent;
}
.btns-counter {
  background: none;
  color: #6b6b6b;
  border: none;
  font-size: 35px;
  cursor: pointer;
  user-select: none;
}

.modal-window_popup__counter-input {
  width: 30px;
}
.modal-window_popup__counter-input input {
  width: 100%;
  border: none;
  background: #fff;
  max-height: 38px;
}
.wrapper-counter {
  display: flex;
  align-items: center;
  max-height: 40px;
  max-width: 130px;
  justify-content: center;
  width: 100%;
  gap: 25px;
  border: 1px solid #1e2227;
  border-radius: 5.61067px;
  padding: 0 10px;
}

.wrapper-svg {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}

.product {
  display: flex;
  max-width: 800px;
  width: 100%;
  gap: 30px;
  margin-top: 70px;
}
.product p {
  font-size: 16px;
}
.product-img img {
  height: 500px;
  width: 400px;
  object-fit: cover;
}
.product-description h3 {
  font-size: 35px;
}
.product-description {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 400px;
  width: 100%;
}
.product-description p {
  text-align: left;
}
.submit {
  padding: 7px 45px;
  background: black;
  color: #ffffff;
  font-size: 14px;
  border: none;
  margin-top: 20px;
}
.product-description_smallvalue {
  display: flex;
  gap: 20px;
}
.product-description_smallvalue > button[selected=true] {
  border-bottom: 1px solid black;
}
.product-description_smallvalue img {
  max-width: 40px;
  width: 100%;
}
.modal-window {
  background: rgba(0, 0, 0, 0.63);
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;
}
.modal-window.active_shadow {
  opacity: 1;
  visibility: visible;
  transition: all 0.3s ease-in-out;
}
.modal-window_popup {
  position: fixed;
  max-width: 800px;
  width: 100%;
  max-height: 600px;
  background: #fff;
  padding: 50px 50px;
  z-index: 101;
  display: none;
}
.modal-window_popup.active_modal_window {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.modal-window_popup h2 {
  display: flex;
  align-items: center;
  justify-content: center;
}
.modal-window_popup__form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.modal-window_popup__form__div {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}
.modal-window_popup__product {
  display: flex;
  align-items: end;
  margin-bottom: 20px;
  gap: 20px;
}
.modal-window_popup__img img {
  width: 30px;
}

.modal-window_popup__input {
  display: flex;
  flex-direction: column;
}
input {
  padding: 8px;
  border-radius: 9px;
  width: 100%;
  border: 1px solid #1e2227;
}
.wrapper-first-column {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 10px;
  max-width: 330px;
  width: 100%;
}
.wrapper-second-column {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 10px;
  max-width: 330px;
  width: 100%;
}

.price {
  font-size: 20px !important;
}

@media (max-width: 480px) {
  .product {
    flex-direction: column;
    margin-top: 30px;
  }
  .product-description {
    max-width: 480px;
    gap: 12px;
    width: 100%;
  }
  .product-img img {
    max-width: 480px;
    width: 102%;
  }

  .modal-window_popup__input span,
  p {
    font-size: 12px;
  }
  .modal-window_popup__input input {
    width: 100%;
    font-size: 11px;
  }
  .modal-window_popup__form, .modal-window_popup__form__div {
    flex-direction: column;
    gap: 7px;
  }
  .wrapper-svg h2 {
    font-size: 14px;
  }

  .modal-window_popup.active_modal_window {
    width: 90%;
    height: 95%;
    padding: 10px 20px 7px 20px;
    justify-content: inherit;
  }
  .modal-window_popup__product {
    margin-bottom: 10px;
  }
  button {
    margin-top: 15px;
  }
  .count p {
    font-size: 18px;
  }
}
</style>
