<template>
  <form class="w-100 d-flex flex-column align-items-center mt-5" action="" @submit="submit">
    <h1>Авторизация</h1>
    <label class="d-flex flex-column mt-4">
      Email
      <input type="email" placeholder="Email" required v-model="email">
    </label>
    <label class="d-flex flex-column mt-2">
      Пароль
      <input type="password" placeholder="Пароль" required v-model="password">
    </label>
    <button class="btn btn-primary mt-4">Отправить</button>
  </form>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "LoginAdminPage",
  components: {  },
  data() {
    return {
      email: "",
      password: ""
    };
  },
  computed: {
    ...mapGetters(["SESSION"]),
  },
  methods: {
    async submit(event){
      event.preventDefault();
      await this.GET_SESSION_ADMIN({email: this.email, password: this.password});
      if(this.SESSION) this.$router.push({ path: "/admin/table_products" });
    },
    ...mapActions(["GET_SESSION_ADMIN"])
  },
};
</script>

