<template>
  <HeaderComponent :color="'black'" />
  <div class="news container">

      <div class="catalog-title">
        <h2>{{ SELECTED_CATEGORY.title }}</h2>
        <span>{{ SELECTED_CATEGORY.description }}</span>
      </div>
      <div
        v-for="collection in COLLECTIONS.rows"
        :key="collection"
        class="news-content"
      >
        <img :src="SERVER_URL + bonusUri + collection.img" alt="" />
        <h3>{{ collection.title }}</h3>
        <span>{{ collection.description }}</span>
        <router-link :to="{ path: '/products/' + collection.id }"
          ><button class="news-btn"><span>Подробнее</span></button></router-link
        >
      </div>
    </div>

    <PaginationComponent
      :links="
        Array(Math.ceil((COLLECTIONS.count || 1) / 3))
          .fill(this.$route.path + '?page=')
          .map((val, i) => (i == 0 ? this.$route.path : val + (i + 1)))
      "
      class="news-pagination"
    />
    <FooterComponent />

</template>

<script>
import HeaderComponent from "@/components/client/HeaderComponent.vue";
import FooterComponent from "@/components/client/FooterComponent.vue";
import PaginationComponent from "@/components/client/PaginationComponent.vue";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "CollectionPage",
  components: {
    HeaderComponent,
    FooterComponent,
    PaginationComponent,
  },
  data() {
    return {
      SERVER_URL: process.env.VUE_APP_SERVER_URL,
      mode: process.env.NODE_ENV,
      bonusUri: process.env.NODE_ENV == "production" ? "/static/" : "/",
    };
  },
  computed: {
    ...mapGetters(["COLLECTIONS", "SELECTED_CATEGORY"]),
  },
  created() {
    /* this.GET_COLLECTIONS({category: this.$route.params.id, page: this.$route.query.page, withoutProducts: true}); */
    this.GET_CATEGORY_BY_ID(this.$route.params.id);
    // if (this.DELETE_FROM_COLLECTIONS) {
    //   this.GET_COLLECTIONS();
    // }
    this.$watch(
      () => this.$route.query,
      () => {
        this.GET_COLLECTIONS({
          category: this.$route.params.id,
          page: this.$route.query.page,
          withoutProducts: true,
        });
      },
      // fetch the data when the view is created and the data is
      // already being observed
      { immediate: true }
    );
  },
  methods: {
    ...mapActions(["GET_COLLECTIONS", "GET_CATEGORY_BY_ID"]),
  },
};
</script>

<style>

.news {
  margin-top: 45px;
}
.news-btn {
  margin-top: 15px;
  font-size: 16px;
  padding: 4px 13px;
  border: none;
  background: black;
  color: white;
}
.news-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
  margin-top: 40px;
}
.news-title h2 {
  font-size: 55px;
}
.news-title span {
  font-size: 20px;
  text-align: center;
}
.news-content img {
  min-height: 100%;
  min-width: 100%;
  object-fit: cover;
  margin-bottom: 25px;
}
.news-content {
  display: flex;
  flex-direction: column;
  margin-top: 3rem;
  max-height: 700px;
}
.news-pagination {
  margin-top: 70px;
}
.catalog-title {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 600px) {
  .news {
    margin-top: 30px;
  }
  .news-btn {
    font-size: 14px;
    padding: 3px 8px;
    border: none;
    background: black;
    color: white;
  }
  .container {
    padding: 0 15px;
  }
  .news-content {
    margin-top: 30px;
  }
}
</style>
