<template>
  <HeaderComponent :color="'black'" />
  <div class="catalog-menu-div container">
    <router-link
      v-for="category in TABLE_CATEGORY.filter((item) => item.title !== 'Подарки')"
      :key="category"
      :to="{ path: '/collection/' + category.id }"
      class="catalog-menu"
    >
      <span>{{ category.title }}</span>
    </router-link>    
  </div>
  <FooterComponent />
</template>

<script>
import HeaderComponent from "@/components/client/HeaderComponent.vue";
import FooterComponent from "@/components/client/FooterComponent.vue";
import { mapGetters } from "vuex";
export default {
  name: "CatalogPage",
  components: { HeaderComponent, FooterComponent },
  data() {
    return {
      SERVER_URL: process.env.VUE_APP_SERVER_URL,
      mode: process.env.NODE_ENV,
      bonusUri: process.env.NODE_ENV == "production" ? "/static/" : "/",
    };
  },
  computed: {
    ...mapGetters(["TABLE_CATEGORY"]),
  },
  created() {
    console.log(this.TABLE_CATEGORY)
  },
  methods: {},
};
</script>

<style>
.catalog-menu-div {
  display: flex;
  flex-wrap: wrap;
  /* max-height: 500px; */
  height: 100%;
  gap: 16px;
}
.catalog-menu-div span {
  color: #fff;
  font-size: 50px;
  z-index: 2;
}
.catalog-menu {
  max-height: 500px;
  width: 100%;
  padding: 250px 0;
  position: relative;
  object-fit: cover;
  background: url(@/assets/news.png) no-repeat center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.catalog-menu:nth-child(3n - 2) {
  max-width: 380px;
  width: 28%;
}

.catalog-menu:nth-child(3n - 1) {
  max-width: 500px;
  width: 40%;
}

.catalog-menu:nth-child(3n) {
  max-width: 380px;
  width: 28%;
}

.catalog-menu::after {
  position: absolute;
  content: "";
  background: rgba(0, 0, 0, 0.55);
  z-index: 1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
@media (min-width: 768px) and (max-width: 900px) {
  .catalog-menu-div {
    display: flex;
    flex-direction: column;
  }
  .catalog-menu:nth-child(n) {
    width: 100%;
    max-width: none;
  }
}
@media (max-width: 768px) {
  .catalog-menu-div {
    gap: 0;
  }

  .catalog-menu:nth-child(3n - 2) {
    max-width: none;
    width: 50%;
  }

  .catalog-menu:nth-child(3n - 1) {
    max-width: none;
    width: 50%;
  }

  .catalog-menu:nth-child(3n) {
    max-width: none;
    width: 100%;
  }

  .catalog-menu {
    padding: 100px 0;
  }
  .catalog-menu span {
    font-size: 18px;
  }
}
</style>
