<template>
  <HeaderAdmin />
  <div class="table-responsive">
    <router-link :to="{ name: 'admin-add_collection' }"
      ><button class="btn btn-primary">Добавить коллекцию</button></router-link
    >
    <table class="table table-hover mt-3">
      <thead>
        <tr class="table-primary">
          <th scope="col">№</th>
          <th scope="col">Наименование коллекции</th>
          <th class="text-center" scope="col">Дата</th>
          <th class="text-center" scope="col">Операции</th>
        </tr>
      </thead>
      <tbody v-for="collection in COLLECTIONS.rows" :key="collection">
        <tr>
          <th scope="row">{{ collection.id }}</th>
          <td>{{ collection.title }}</td>
          <td class="text-center">{{ dateTime(collection.updatedAt) }}</td>
          <td class="text-center">
            <div
              class="btn-group"
              role="group"
              aria-label="Basic mixed styles example"
              @submit.prevent
            >
              <button
                type="button"
                class="btn btn-success"
                data-bs-toggle="modal"
                data-bs-target="#editModal"
                @click="SELECTED_COLLECTION = collection"
              >
                Редактировать
              </button>
              <button
                type="button"
                class="btn btn-danger"
                @click="DELETE_FROM_COLLECTION(collection.id)"
              >
                Удалить
              </button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
  <div
    class="modal fade"
    id="editModal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">
            Редактирование коллекции
          </h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <div class="mb-3">
            <label class="form-label w-100">
              Наименование коллекции
              <input
                type="text"
                class="form-control mt-1"
                id="formGroupExampleInput"
                v-model="SELECTED_COLLECTION.title"
              />
            </label>
            <label class="form-label w-100">
              Описание коллекции
              <textarea
                type="text"
                class="form-control mt-1"
                id="formGroupExampleInput2"
                placeholder="Введите название категории"
                v-model="SELECTED_COLLECTION.description"
              ></textarea>
            </label>
            <label class="form-label w-100">
              Фотография коллекции
              <input
                type="file"
                class="form-control mt-1"
                ref="file"
                required
              />
            </label>
            <div class="form-group">
              <label for="formGroupExampleInput" class="form-label"
                >Товар коллекции</label
              >
              <select
                class="form-select"
                aria-label="Default select example"
                multiple
                v-model="SELECTED_COLLECTION.productIds"
                required
              >
                <option
                  :value="product"
                  v-for="product in PRODUCTS.rows"
                  :key="product"
                >
                  {{ product.title }}
                </option>
              </select>
            </div>
            <label class="form-label w-100">
              Категория коллекции
              <select
                class="form-select"
                aria-label="Default select example"
                v-model="SELECTED_COLLECTION.categoryId"
                required
              >
                <option
                  :value="category.id"
                  v-for="category in TABLE_CATEGORY"
                  :key="category"
                >
                  {{ category.title }}
                </option>
              </select>
            </label>
          </div>
        </div>
        <div @submit.prevent class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary"
            data-bs-dismiss="modal"
          >
            Закрыть
          </button>
          <button
            type="button"
            class="btn btn-primary"
            data-bs-dismiss="modal"
            @click="this.edit(SELECTED_COLLECTION.id)"
          >
            Редактировать
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HeaderAdmin from "@/components/admin_components/HeaderAdmin.vue";
import { mapActions, mapGetters } from "vuex";
import moment from "moment";
import axios from "axios";
export default {
  name: "TableProductsAdminPage",
  components: { HeaderAdmin },
  data() {
    return {
      SELECTED_COLLECTION: {},
    };
  },
  computed: {
    ...mapGetters(["COLLECTIONS", "TABLE_CATEGORY", "PRODUCTS"]),
  },
  mounted() {
    this.GET_COLLECTIONS({ limit: 100 });
    this.GET_PRODUCTS();
  },
  methods: {
    async edit(id) {
      const data = {
        ...this.$data,
        title: this.SELECTED_COLLECTION.title,
        description: this.SELECTED_COLLECTION.description,
        categoryId: this.SELECTED_COLLECTION.categoryId,
        productIds: this.SELECTED_COLLECTION.productIds,
        img: this.SELECTED_COLLECTION.img,
      };
      delete this.SELECTED_COLLECTION.img;
      if (this.$refs.file.files[0])
        this.SELECTED_COLLECTION.img = this.$refs.file.files[0];
      await axios({
        method: "PUT",
        url: process.env.VUE_APP_SERVER_URL + `/api/admin/collection/${id}`,
        headers: {
          "Content-Type": "multipart/form-data",
        },
        data,
      }).then(() => {
        this.GET_COLLECTIONS({ limit: 100 });
        console.log(this.SELECTED_COLLECTION);
        console.log(this.SELECTED_COLLECTION.productIds);
        console.log(this.products);
      });
    },
    ...mapActions([
      "GET_COLLECTIONS",
      "DELETE_FROM_COLLECTION",
      "GET_CATEGORY",
      "GET_PRODUCTS",
    ]),
    dateTime(value) {
      return moment(value).format("DD.MM.YYYY");
    },
  },
};
</script>

<style scoped>
.table-responsive {
  background-color: #f8f8f8;
  border-radius: 10px;
  box-shadow: 0px 0px 9px -2px rgba(0, 0, 0, 0.2);
  padding: 0 10px 0 10px;
  margin-top: 20px;
}

tbody,
td,
tfoot,
th,
thead,
tr {
  border-style: none;
}

.btn {
  box-shadow: none;
}

.btn-group {
  gap: 10px;
}
</style>
