<template>
  <HeaderAdmin />
  <div class="table-responsive">
    <table class="table table-hover mt-3">
      <thead>
        <tr class="table-primary">
          <th scope="col">№</th>
          <th class="text-center" scope="col">Товар</th>
          <th class="text-center" scope="col">Получатель</th>
          <th class="text-center" scope="col">Телефон</th>
          <th class="text-center" scope="col">Email</th>
          <th class="text-center" scope="col">Адрес</th>
          <th class="text-center" scope="col">Цена</th>
          <th class="text-center" scope="col">Дата</th>
          <th class="text-center" scope="col">Операции</th>
        </tr>
      </thead>
      <tbody v-for="purchase in PURCHASES.rows" :key="purchase">
        <tr>
          <th scope="row">{{ purchase.id }}</th>
          <td class="text-center">
            <button class="show-purchases-btn" @click="purchase.show = !purchase.show">Развернуть список товаров</button>
            <div class="show-purchases-list mt-2" :hidden="!purchase.show">
              <span class="fs-6 my-1" v-for="product in purchase.products" :key="product"><router-link :to="{ path: '/about_product/' + product.id }">{{ product.title  }}</router-link> ({{ product.product_purchase.count  }}) - {{ product.priceAndVolume[product.product_purchase.volume].volume }}мл - {{ product.product_purchase.count * product.priceAndVolume[product.product_purchase.volume].price }} р.</span>
            </div>
          </td>
          <td class="text-center">{{ purchase.name }}</td>
          <td class="text-center">{{ purchase.phone_number }}</td>
          <td class="text-center">{{ purchase.email }}</td>
          <td class="text-center">{{ purchase.city }}</td>
          <td class="text-center">{{ getPrice(purchase) }} р.</td>
          <td class="text-center">{{ dateTime(purchase.updatedAt) }}</td>
          <td class="text-center">
            <div
              class="btn-group"
              role="group"
              aria-label="Basic mixed styles example"
              @submit.prevent
            >
              <!-- <router-link
                :to="{
                  name: 'admin-add-products',
                  params: { id: purchase.id },
                }"
              >
                <button
                  type="button"
                  class="btn btn-success"
                  data-bs-toggle="modal"
                  data-bs-target="#editModal"
                >
                  Редактировать
                </button>
              </router-link> -->
              <button
                type="button"
                class="btn btn-danger"
                @click="DELETE_FROM_PURCHASES(purchase.id)"
              >
                Удалить
              </button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import HeaderAdmin from "@/components/admin_components/HeaderAdmin.vue";
import { mapActions, mapGetters } from "vuex";
import moment from "moment";
export default {
  name: "TablePurchasesAdminPage",
  components: { HeaderAdmin },
  computed: {
    ...mapGetters(["PURCHASES"]),
  },
  mounted() {
    this.GET_PURCHASES({ limit: 100 });
    // if (this.DELETE_FROM_PURCHASES) {
    //   this.GET_PURCHASES();
    // }
  },
  methods: {
    getPrice(purchase){
      var price = 0;
      for(const product of purchase.products){
        price += product.product_purchase.count * product.priceAndVolume[product.product_purchase.volume].price;
      }
      return price;
    },
    ...mapActions(["GET_PURCHASES", "DELETE_FROM_PURCHASES"]),
    dateTime(value) {
      return moment(value).format("DD.MM.YYYY");
    },
  },
};
</script>

<style scoped>
.table-responsive {
  background-color: #f8f8f8;
  border-radius: 10px;
  box-shadow: 0px 0px 9px -2px rgba(0, 0, 0, 0.2);
  padding: 0 10px 0 10px;
  margin-top: 20px;
}

tbody,
td,
tfoot,
th,
thead,
tr {
  border-style: none;
}

.btn {
  box-shadow: none;
}

.btn-group {
  gap: 10px;
}

.show-purchases-btn {
  border: none;
  background: transparent;
  color: blue;
  padding: 2px;
}

.show-purchases-btn:hover {
  background: rgba(0, 0, 0, 0.2);
}

.show-purchases-list {
  display: flex;
  flex-direction: column;
}
</style>
