import { createStore } from "vuex";
import axios from "axios";

export default createStore({
  state: {
    tableCategory: [],
    selectCategory: {},
    products: [],
    s_products: {},
    purchases: [],
    collections: [],
    product: {},
    session: "",
    address: {
      count: 0,
      rows: []
    },
    cart: []
  },
  getters: {
    TABLE_CATEGORY(state) {
      return state.tableCategory;
    },
    SELECTED_CATEGORY(state) {
      return state.selectCategory;
    },
    PRODUCTS(state) {
      return state.products
    },
    S_PRODUCTS(state) {
      return state.s_products
    },
    PRODUCT(state) {
      return state.product
    },
    PURCHASES(state) {
      return state.purchases
    },
    COLLECTIONS(state) {
      return state.collections
    },
    SESSION(state) {
      return state.session
    },
    ADDRESS(state) {
      return state.address
    },
    CART(state) {
      return state.cart
    },
  },
  mutations: {
    SET_CATEGORY(state, data) {
      state.tableCategory = data;
    },
    DELETE_CATEGORY(state, id) {
      state.tableCategory.splice(id, 1);
    },
    DELETE_PRODUCT(state, id) {
      state.products.splice(id, 1)
    },
    DELETE_COLELCTION(state, id){
      state.collections.splice(id, 1);
    },
    DELETE_PURCHASE(state, id) {
      state.purchases.splice(id, 1)
    },
    SET_SELECT_CATEGORY(state, data) {
      state.selectCategory = data;
    },
    SET_PRODUCTS(state, data) {
      state.products = data
    },
    SET_S_PRODUCTS(state, data) {
      state.s_products = data
    },
    SET_PRODUCT(state, data) {
      state.product = data
    },
    SET_PURCHASES(state, data) {
      state.purchases = data
    },
    SET_COLLECTIONS(state, data) {
      state.collections = data
    },
    SET_SESSION(state, data) {
      state.session = data
    },
    SET_ADDRESS(state, data) {
      state.address = data
    },
    SET_CART(state, data) {
      state.cart = data
    },
    ADD_CART(state, data) {
      state.cart = [...state.cart, data];
    },
    REMOVE_FROM_CART(state, id) {
      state.cart.splice(id, 1);
      state.cart = [...state.cart];
    },
  },
  actions: {
    async GET_CATEGORY({ commit }, params = {}) {
      return await axios({
        method: "GET",
        url: process.env.VUE_APP_SERVER_URL + "/api/admin/category",
        params
      })
        .then((response) => {
          commit("SET_CATEGORY", response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async GET_PRODUCTS({ commit }, params = {}) {
      return await axios({
        method: "GET",
        url: process.env.VUE_APP_SERVER_URL + "/api/admin/products",
        params
      })
        .then((response) => {
          commit("SET_PRODUCTS", response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async SEARCH_PRODUCTS({ commit }, params = {}) {
      return await axios({
        method: "GET",
        url: process.env.VUE_APP_SERVER_URL + "/api/admin/products",
        params
      })
        .then((response) => {
          commit("SET_S_PRODUCTS", response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async GET_COLLECTIONS({ commit }, params = {}) {
      return await axios({
        method: "GET",
        url: process.env.VUE_APP_SERVER_URL + "/api/admin/collection",
        params
      })
        .then((response) => {
          commit("SET_COLLECTIONS", response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async GET_PURCHASES({ commit }, params = {}) {
      return await axios({
        method: "GET",
        url: process.env.VUE_APP_SERVER_URL + "/api/admin/user_client",
        params
      })
        .then((response) => {
          commit("SET_PURCHASES", response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async DELETE_FROM_TABLE_CATEGORY({ commit, dispatch }, id) {
      return await axios({
        method: "DELETE",
        url: process.env.VUE_APP_SERVER_URL + `/api/admin/category/${id}`,
      })
        .then((response) => {
          commit("DELETE_CATEGORY", response.data.id);
          dispatch("GET_CATEGORY");
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async DELETE_FROM_PRODUCTS({commit, dispatch}, id) {
      return await axios({
        method: 'DELETE',
        url: process.env.VUE_APP_SERVER_URL + `/api/admin/products/${id}`,
      })
      .then((response) => {
        console.log(response);
        commit('DELETE_PURCHASE', response.data.id)
        dispatch('GET_PRODUCTS')
      })
    },
    async DELETE_FROM_COLLECTION({commit, dispatch}, id) {
      return await axios({
        method: 'DELETE',
        url: process.env.VUE_APP_SERVER_URL + `/api/admin/collection/${id}`,
      })
      .then((response) => {
        console.log(response);
        commit("DELETE_COLLECTION", response.data.id)
        dispatch('GET_COLLECTIONS', {limit: 100});
      })
    },
    async DELETE_FROM_PURCHASES({dispatch}, id) {
      return await axios({
        method: 'DELETE',
        url: process.env.VUE_APP_SERVER_URL + `/api/admin/user_client/${id}`,
      })
      .then((response) => {
        console.log(response);
        dispatch('GET_PURCHASES');
      })
    },
    async GET_CATEGORY_BY_ID({ commit }, id) {
      return await axios({
        method: "GET",
        url: process.env.VUE_APP_SERVER_URL + `/api/admin/category/${id}`,
      })
        .then((response) => {
          commit("SET_SELECT_CATEGORY", response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async GET_PRODUCT_BY_ID({ commit }, id) {
      return await axios({
        method: "GET",
        url: process.env.VUE_APP_SERVER_URL + `/api/admin/products/${id}`,
      })
        .then((response) => {
          commit("SET_PRODUCT", response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async GET_SESSION_ADMIN({ commit }, data) {
      return await axios({
        method: "POST",
        url: process.env.VUE_APP_SERVER_URL + `/api/admin/user/login_admin`,
        data
      })
        .then((response) => {
          commit("SET_SESSION", response.data.token);
          axios.defaults.headers.common['session'] = response.data.token;
          localStorage.setItem("token", response.data.token);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async GET_ADDRESS({ commit }, params={}) {
      return await axios({
        method: "GET",
        url: process.env.VUE_APP_SERVER_URL + `/api/admin/store_address`,
        params
      })
        .then((response) => {
          commit("SET_ADDRESS", response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async UPDATE_ADDRESS({ commit }, {id, data}) {
      return await axios({
        method: "PUT",
        url: process.env.VUE_APP_SERVER_URL + `/api/admin/store_address/${id}`,
        data
      })
        .then((response) => {
          commit("SET_ADDRESS", response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async DELETE_ADDRESS({dispatch}, id) {
      return await axios({
        method: 'DELETE',
        url: process.env.VUE_APP_SERVER_URL + `/api/admin/store_address/${id}`,
      })
      .then((response) => {
        console.log(response);
        dispatch('GET_ADDRESS');
      })
    },
    async UPDATE_CART({ commit }, cart) {
      if(!cart.length) return;
      const volumeAndCountById = {};
      for(const item of cart){
        volumeAndCountById[item.id] = [item.volume, item.count];
      }
      return await axios({
        method: "GET",
        url: process.env.VUE_APP_SERVER_URL + `/api/admin/products`,
        params: {
          list: cart.map(item => item.id)
        }
      })
        .then((response) => {
          commit("SET_CART", response.data.rows.map(item => ({ ...item, volume: volumeAndCountById[item.id][0], count: volumeAndCountById[item.id][1] })));
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async ADD_CART_PRODUCT({ commit, state }, product) {
      commit("ADD_CART", product);
      localStorage.setItem("cart", JSON.stringify(state.cart.map(item => ({ id: item.id, volume: item.volume, count: item.count }))));
    },
    async REMOVE_FROM_CART({ commit, state }, id) {
      commit("REMOVE_FROM_CART", id);
      localStorage.setItem("cart", JSON.stringify(state.cart.map(item => ({ id: item.id, volume: item.volume, count: item.count }))));
    },
    async CLEAR_CART({ commit }) {
      commit("SET_CART", []);
      localStorage.setItem("cart", "[]");
    },
    async SET_SESSION_ADMIN({ commit }, token) {
      commit("SET_SESSION", token);
      axios.defaults.headers.common['session'] = token;
      localStorage.setItem("token", token);
    },
  },
  modules: {},
});
