<template>
  <HeaderAdmin/>
  <div class="category_admin_views">
    <button
      type="button"
      class="btn btn-primary mt-3"
      data-bs-toggle="modal"
      data-bs-target="#exampleModal"
    >
      Добавить категорию
    </button>
    <div
      class="modal fade"
      id="exampleModal"
      tabindex="-1"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">
              Добавление категории
            </h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body">
            <div class="mb-3">
              <label for="formGroupExampleInput" class="form-label"
                >Наименование категории</label
              >
              <input
                type="text"
                class="form-control"
                id="formGroupExampleInput"
                placeholder="Введите название категории"
                v-model="category.title"
              />
              <label for="formGroupExampleInput2" class="form-label"
                >Описание категории</label
              >
              <input
                type="text"
                class="form-control"
                id="formGroupExampleInput2"
                placeholder="Введите название категории"
                v-model="category.description"
              />
            </div>
          </div>
          <div @submit.prevent class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-bs-dismiss="modal"
            >
              Закрыть
            </button>
            <button
              @click="onSubmit"
              type="button"
              class="btn btn-primary"
              data-bs-dismiss="modal"
            >
              Добавить
            </button>
          </div>
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <table class="table table-hover mt-3">
        <thead>
          <tr class="table-primary">
            <th scope="col">№</th>
            <th scope="col">Заголовок</th>
            <th scope="col">Дата</th>
            <th scope="col">Операции</th>
          </tr>
        </thead>
        <tbody v-for="table_cat in TABLE_CATEGORY" :key="table_cat">
          <tr>
            <th scope="row">{{ table_cat.id }}</th>
            <td>{{ table_cat.title }}</td>
            <td>{{ dateTime(table_cat.updatedAt) }}</td>
            <td>
              <div
                class="btn-group"
                role="group"
                aria-label="Basic mixed styles example"
                @submit.prevent
              >
                <button
                  type="button"
                  class="btn btn-success"
                  data-bs-toggle="modal"
                  data-bs-target="#editModal"
                  @click="GET_CATEGORY_BY_ID(table_cat.id)"
                >
                  Редактировать
                </button>
                <button
                  type="button"
                  class="btn btn-danger"
                  @click="DELETE_FROM_TABLE_CATEGORY(table_cat.id)"
                >
                  Удалить
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div
      class="modal fade"
      id="editModal"
      tabindex="-1"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">
              Редактирование категории
            </h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body">
            <div class="mb-3">
              <label for="formGroupExampleInput" class="form-label"
                >Наименование категории</label
              >
              <input
                type="text"
                class="form-control"
                id="formGroupExampleInput"
                v-model="SELECTED_CATEGORY.title"
              />
              <label for="formGroupExampleInput2" class="form-label"
                >Описание категории</label
              >
              <input
                type="text"
                class="form-control"
                id="formGroupExampleInput2"
                placeholder="Введите название категории"
                v-model="SELECTED_CATEGORY.description"
              />
            </div>
          </div>
          <div @submit.prevent class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-bs-dismiss="modal"
            >
              Закрыть
            </button>
            <button
              type="button"
              class="btn btn-primary"
              data-bs-dismiss="modal"
              @click="this.toEditCAtegory(SELECTED_CATEGORY.id)"
            >
              Редактировать
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import HeaderAdmin from '@/components/admin_components/HeaderAdmin.vue'
import moment from "moment";
import axios from "axios";

export default {
  name: "CategoryAdminPage",
  components: {HeaderAdmin},
  data() {
    return {
      category: {
        title: "",
        description: ""
      },
      select_category: {
        title: "",
        description: ""
      },
    };
  },
  mounted() {
    this.GET_CATEGORY();
  },
  computed: {
    ...mapGetters(["TABLE_CATEGORY", "SELECTED_CATEGORY"]),
  },
  methods: {
    ...mapActions([
      "GET_CATEGORY",
      "DELETE_FROM_TABLE_CATEGORY",
      "GET_CATEGORY_BY_ID",
    ]),
    dateTime(value) {
      return moment(value).format("DD.MM.YYYY");
    },
    async onSubmit() {
      await axios({
        method: "POST",
        url: process.env.VUE_APP_SERVER_URL + "/api/admin/category",
        data: this.category
      }).then(() => {
        this.category = {
          title: "",
          description: ""
        }
        this.GET_CATEGORY();
      });
    },
    async toEditCAtegory(id) {
      await axios({
        method: "PUT",
        url: process.env.VUE_APP_SERVER_URL + `/api/admin/category/${id}`,
        data: this.SELECTED_CATEGORY
      }).then(() => {
        this.GET_CATEGORY();
      });
    },
  },
};
</script>

<style scoped>
.category_admin_views {
  margin-left: 80px;
  margin-right: 80px;
}

.table-responsive {
  background-color: #f8f8f8;
  border-radius: 10px;
  box-shadow: 0px 0px 9px -2px rgba(0, 0, 0, 0.2);
  padding: 0 10px 0 10px;
  margin-top: 20px;
}

tbody, td, tfoot, th, thead, tr  {
  border-style: none;
}

.btn {
  box-shadow: none;
}

.btn-group {
  gap: 10px;
}
</style>
