<template>
  <HeaderAdmin/>
  <div>
    <form action="" class="form-collection" @submit="submit">
      <div class="mt-3">
        <div class="form-group">
          <label for="formGroupExampleInput" class="form-label"
            >Название коллекции</label
          >
          <input type="text" class="form-control" id="formGroupExampleInput" v-model="title" required />
        </div>
        <div class="form-group">
          <label for="exampleFormControlTextarea1" class="form-label"
            >Описание коллекции</label
          >
          <textarea
            class="form-control"
            id="exampleFormControlTextarea1"
            rows="3"
            v-model="description"
            required
          ></textarea>
        </div>
        <div class="form-group">
          <label for="formGroupExampleInput" class="form-label"
            >Фотографию коллекции</label
          >
          <input type="file" class="form-control" id="formGroupExampleInput" ref="file" required />
        </div>
        <div class="form-group">
          <label for="formGroupExampleInput" class="form-label"
            >Выберите продукты</label
          >
          <select class="form-select" aria-label="Default select example" multiple v-model="products" required>
            <option :value="product" v-for="product in PRODUCTS.rows" :key="product">{{ product.title }}</option>
          </select>
        </div>
        <div class="form-group">
          <label for="formGroupExampleInput" class="form-label"
            >Выберите категорию</label
          >
          <select class="form-select" aria-label="Default select example" v-model="categoryId" required>
            <option :value="category" v-for="category in TABLE_CATEGORY" :key="category">{{ category.title }}</option>
          </select>
        </div>
        <button class="btn btn-primary mt-2">Отправить</button>
      </div>
    </form>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
import HeaderAdmin from '@/components/admin_components/HeaderAdmin.vue';
import axios from 'axios';
export default {
  name: "FormCollection",
  components: {HeaderAdmin},
  data(){
    return {
      title: "", 
      description: "", 
      categoryId: 1, 
      products: []
    }
  },
  mounted() {
    this.GET_PRODUCTS();
    this.GET_CATEGORY();
  },
  computed:{
    ...mapGetters(["PRODUCTS", "TABLE_CATEGORY"])
  },
  methods: {
    submit(event){
      event.preventDefault();
      console.log({...this.$data, categoryId: this.categoryId.id, products: Array.from(this.products).map(item => item.id)});
      const img = this.$refs.file.files[0];
      const data = {...this.$data, categoryId: this.categoryId.id, products: Array.from(this.products).map(item => item.id), img};
      axios({
        method: "POST",
        url: process.env.VUE_APP_SERVER_URL + "/api/admin/collection",
        headers: {
          "Content-Type": "multipart/form-data",
        },
        data
      }).then(() => {
        console.log(this.products);
        this.$router.push({ path: "/admin/collection" });
      }).catch((err) => {
        console.log(err);
      });
    },
    ...mapActions(["GET_PRODUCTS", "GET_CATEGORY"])
  },
  
};
</script>

<style scoped>
.form-collection {
  margin-left: 80px;
  width: 600px;
}

.mt-3 {
  display: flex;
  flex-direction: column;
  gap: 25px;
}

select[multiple] {
  height: 200px;
}
</style>
