<template>
  <HeaderAdmin />
  <div>
    <form action="" class="form-collection" @submit.prevent>
      <div class="mt-3 d-flex flex-column gap-4">
        <div class="form-group">
          <label for="formGroupExampleInput" class="form-label"
            >Наименование товара</label
          >
          <input
            type="text"
            class="form-control"
            id="formGroupExampleInput"
            v-model="title"
          />
        </div>
        <div class="form-group">
          <label for="exampleFormControlTextarea1" class="form-label"
            >Описание товара</label
          >
          <textarea
            class="form-control"
            id="exampleFormControlTextarea1"
            rows="3"
            v-model="description"
          ></textarea>
        </div>
        <div class="form-group">
          <label for="formGroupExampleInput" class="form-label"
            >Фотография товара</label
          >
          <input
            type="file"
            class="form-control"
            id="formGroupExampleInput"
            ref="file"
          />
        </div>
        <div class="form-group d-flex flex-column">
          <label for="formGroupExampleInput" class="form-label"
            >Объем и цена товара</label
          >
          <button
            type="button"
            class="btn btn-primary me-auto"
            @click="addProductInput"
          >
            Добавить цену и объем
          </button>
          <div class="formPriceAndVolume">
            <ul class="mt-2">
              <li
                class="mt-3"
                v-for="(item, index) in productsList"
                :key="index"
              >
                <span class="text-dark">Цена {{ index + 1 }}</span>
                <input
                  type="number"
                  placeholder="Цена"
                  class="form-control"
                  v-model="item.price"
                  id="formGroupExampleInput"
                />
                <input
                  type="number"
                  placeholder="Объем"
                  class="form-control mt-1"
                  v-model="item.volume"
                  id="formGroupExampleInput"
                />
              </li>
            </ul>
          </div>
        </div>
      </div>
      <button type="button" class="btn btn-success" @click="addProduct">
        Success
      </button>
    </form>
  </div>
</template>

<script>
import HeaderAdmin from "@/components/admin_components/HeaderAdmin.vue";
import axios from "axios";
import {mapActions, mapGetters} from 'vuex'
export default {
  name: "ProductsAdminPage",
  components: { HeaderAdmin },
  data() {
    return {
      title: "",
      description: "",
      img: "",
      productsList: [],
    };
  },
  
  mounted() {
    this.GET_COLLECTIONS()
  },
  computed: {
    ...mapGetters(["COLLECTIONS"])
  },
  methods: {
    ...mapActions(["GET_PRODUCTS", "GET_COLLECTIONS"]),
    async addProduct() {
      this.img = this.$refs.file.files[0];
      const formData = new FormData();
      formData.append("img", this.img);
      console.log(formData);
      await axios({
        method: "POST",
        url: process.env.VUE_APP_SERVER_URL + "/api/admin/products",
        formData,
        headers: {
          "Content-Type": "multipart/form-data",
        },
        data: {
          title: this.title,
          description: this.description,
          img: this.img,
          priceAndVolume: JSON.stringify(this.productsList),
        },
        
      }).then((response) => {
        console.log(response.data);
        this.$router.push({ path: "/admin/table_products" });
      });
    },
    addProductInput() {
      this.productsList.push({ price: null, volume: null });
    },
  },
};
</script>

<style scoped>
.form-collection {
  margin-left: 80px;
  width: 600px;
}
.mt-3 {
  display: flex;
  flex-direction: column;
  gap: 25px;
}
.btn {
  margin-top: 20px;
}
</style>
