<template>
  <HeaderAdmin />
  <div class="table-responsive">
    <router-link :to="{name: 'admin-add-products'}"><button class="btn btn-primary">Добавить продукт</button></router-link>
    <table class="table table-hover mt-3">
      <thead>
        <tr class="table-primary">
          <th scope="col">№</th>
          <th scope="col">Наименование товара</th>
          <th class="text-center" scope="col">Дата</th>
          <th class="text-center" scope="col">Операции</th>
        </tr>
      </thead>
      <tbody v-for="table_prod in PRODUCTS.rows" :key="table_prod">
        <tr>
          <th scope="row">{{ table_prod.id }}</th>
          <td>{{ table_prod.title }}</td>
          <td class="text-center">{{ dateTime(table_prod.updatedAt) }}</td>
          <td class="text-center">
            <div
              class="btn-group"
              role="group"
              aria-label="Basic mixed styles example"
              @submit.prevent
            >
              <button
                type="button"
                class="btn btn-success"
                data-bs-toggle="modal"
                data-bs-target="#editModal"
                @click="SELECTED_PRODUCT = table_prod"
              >
                Редактировать
              </button>
              <button
                type="button"
                class="btn btn-danger"
                @click="DELETE_FROM_PRODUCTS(table_prod.id)"
              >
                Удалить
              </button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
  <div
    class="modal fade"
    id="editModal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">
            Редактирование продукта
          </h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <div class="mb-3">
            <label class="form-label w-100">
              Наименование продукта
              <input
                type="text"
                class="form-control mt-1"
                id="formGroupExampleInput"
                v-model="SELECTED_PRODUCT.title"
              />
            </label>
            <label class="form-label w-100">
              Описание продукта
              <textarea
                type="text"
                class="form-control mt-1"
                id="formGroupExampleInput2"
                placeholder="Введите название категории"
                v-model="SELECTED_PRODUCT.description"
              ></textarea>
            </label>
            <label class="form-label w-100">
              Фотография продукта
              <input type="file" class="form-control mt-1" ref="file" required />
            </label>
            <div class="form-group">
        </div>
            <div class="d-flex flex-column w-100">
              Цена и объем продукта
              <button
                type="button"
                class="btn btn-primary me-auto mt-2"
                @click="SELECTED_PRODUCT.priceAndVolume.push({ price: undefined, volume: undefined })"
              >
                Добавить цену и объем
              </button>
              <div>
                <ul class="mt-2">
                  <li
                    class="mt-3"
                    v-for="(item, index) in SELECTED_PRODUCT.priceAndVolume"
                    :key="index"
                  >
                    <span class="text-dark">Цена {{ index + 1 }}</span>
                    <input
                      type="number"
                      placeholder="Цена"
                      class="form-control"
                      v-model="item.price"
                      id="formGroupExampleInput"
                    />
                    <input
                      type="number"
                      placeholder="Объем"
                      class="form-control mt-1"
                      v-model="item.volume"
                      id="formGroupExampleInput"
                    />
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
        <div @submit.prevent class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary"
            data-bs-dismiss="modal"
          >
            Закрыть
          </button>
          <button
            type="button"
            class="btn btn-primary"
            data-bs-dismiss="modal"
            @click="this.edit(SELECTED_PRODUCT.id)"
          >
            Редактировать
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HeaderAdmin from "@/components/admin_components/HeaderAdmin.vue";
import { mapActions, mapGetters } from "vuex";
import moment from "moment";
import axios from "axios";
export default {
  name: "TableProductsAdminPage",
  components: { HeaderAdmin },
  computed: {
    ...mapGetters(["PRODUCTS", "COLLECTIONS"]),
  },
  data() {
    return {
      SELECTED_PRODUCT: {}
    }
  },
  mounted() {
    this.GET_PRODUCTS();
  },
  methods: {
    async edit(id) {
      if(Array.isArray(this.SELECTED_PRODUCT.priceAndVolume))this.SELECTED_PRODUCT.priceAndVolume = JSON.stringify(this.SELECTED_PRODUCT.priceAndVolume);
      delete this.SELECTED_PRODUCT.img;
      if(this.$refs.file.files[0])this.SELECTED_PRODUCT.img = this.$refs.file.files[0];
      await axios({
        method: "PUT",
        url: process.env.VUE_APP_SERVER_URL + `/api/admin/products/${id}`,
        headers: {
          "Content-Type": "multipart/form-data",
        },
        data: {
          title: this.SELECTED_PRODUCT.title, 
          description: this.SELECTED_PRODUCT.description,
          priceAndVolume: this.SELECTED_PRODUCT.priceAndVolume,
          img: this.SELECTED_PRODUCT.img,
        }
      }).then(() => {
        this.GET_PRODUCTS();
        console.log(this.SELECTED_PRODUCT);
        console.log(this.PRODUCTS.rows);
      });
    },
    ...mapActions(["GET_PRODUCTS", "DELETE_FROM_PRODUCTS"]),
    dateTime(value) {
      return moment(value).format("DD.MM.YYYY");
    },
  },
};
</script>

<style scoped>
.table-responsive {
  background-color: #f8f8f8;
  border-radius: 10px;
  box-shadow: 0px 0px 9px -2px rgba(0, 0, 0, 0.2);
  padding: 0 10px 0 10px;
  margin-top: 20px;
}

tbody,
td,
tfoot,
th,
thead,
tr {
  border-style: none;
}

.btn {
  box-shadow: none;
}

.btn-group {
  gap: 10px;
}
</style>
