<template>
  <HeaderAdmin/>
  <div class="category_admin_views">
    <button
      type="button"
      class="btn btn-primary mt-3"
      data-bs-toggle="modal"
      data-bs-target="#exampleModal"
    >
      Добавить место
    </button>
    <div
      class="modal fade"
      id="exampleModal"
      tabindex="-1"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">
              Добавление места
            </h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body">
            <div class="mb-3">
              <label class="form-label w-100">
                Адрес места
                <input
                  type="text"
                  class="form-control mt-1"
                  id="formGroupExampleInput"
                  placeholder="Адрес места"
                  v-model="new_address.address"
                />
              </label>
              <label class="form-label w-100">
                Время работы места
                <input
                  type="text"
                  class="form-control mt-1"
                  id="formGroupExampleInput"
                  placeholder="Время работы места"
                  v-model="new_address.time"
                />
              </label>
              <label class="form-label w-100">
                Фотография места
                <input type="file" class="form-control mt-1" ref="file" required />
              </label>
            </div>
          </div>
          <div @submit.prevent class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-bs-dismiss="modal"
            >
              Закрыть
            </button>
            <button
              @click="onSubmit"
              type="button"
              class="btn btn-primary"
              data-bs-dismiss="modal"
            >
              Добавить
            </button>
          </div>
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <table class="table table-hover mt-3">
        <thead>
          <tr class="table-primary">
            <th scope="col">№</th>
            <th scope="col">Адрес</th>
            <th scope="col">Время</th>
            <th scope="col">Операции</th>
          </tr>
        </thead>
        <tbody v-for="address in ADDRESS.rows" :key="address">
          <tr>
            <th scope="row">{{ address.id }}</th>
            <td>{{ address.address }}</td>
            <td>{{ address.time }}</td>
            <td>
              <div
                class="btn-group"
                role="group"
                aria-label="Basic mixed styles example"
                @submit.prevent
              >
                <button
                  type="button"
                  class="btn btn-success"
                  data-bs-toggle="modal"
                  data-bs-target="#editModal"
                  @click="select_address = address"
                >
                  Редактировать
                </button>
                <button
                  type="button"
                  class="btn btn-danger"
                  @click="DELETE_ADDRESS(address.id)"
                >
                  Удалить
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div
      class="modal fade"
      id="editModal"
      tabindex="-1"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">
              Редактирование категории
            </h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body">
            <label class="form-label w-100">
              Адрес места
              <input
                type="text"
                class="form-control mt-1"
                id="formGroupExampleInput"
                placeholder="Адрес места"
                v-model="select_address.address"
              />
            </label>
            <label class="form-label w-100">
              Время работы места
              <input
                type="text"
                class="form-control mt-1"
                id="formGroupExampleInput"
                placeholder="Время работы места"
                v-model="select_address.time"
              />
            </label>
            <label class="form-label w-100">
              Фотография места
              <input type="file" class="form-control mt-1" ref="file2" required />
            </label>
          </div>
          <div @submit.prevent class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-bs-dismiss="modal"
            >
              Закрыть
            </button>
            <button
              type="button"
              class="btn btn-primary"
              data-bs-dismiss="modal"
              @click="this.edit(select_address.id)"
            >
              Редактировать
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import HeaderAdmin from '@/components/admin_components/HeaderAdmin.vue';
import axios from "axios";

export default {
  name: "CategoryAdminPage",
  components: {HeaderAdmin},
  data() {
    return {
      new_address: {
        address: "",
        time: ""
      },
      select_address: {
        address: "",
        time: ""
      },
    };
  },
  mounted() {
    this.GET_ADDRESS({ limit: 100 });
  },
  computed: {
    ...mapGetters(["ADDRESS"]),
  },
  methods: {
    ...mapActions([
      "GET_ADDRESS",
      "DELETE_ADDRESS"
    ]),
    async onSubmit() {
      delete this.new_address.img;
      if(this.$refs.file.files[0]) this.new_address.img = this.$refs.file.files[0];
      await axios({
        method: "POST",
        url: process.env.VUE_APP_SERVER_URL + "/api/admin/store_address",
        headers: {
          "Content-Type": "multipart/form-data",
        },
        data: this.new_address
      }).then(() => {
        this.new_address = {
          address: "",
          time: ""
        }
        this.GET_ADDRESS({ limit: 100 });
      });
    },
    async edit(id) {
      delete this.select_address.img;
      if(this.$refs.file2.files[0]) this.select_address.img = this.$refs.file2.files[0];
      await axios({
        method: "PUT",
        url: process.env.VUE_APP_SERVER_URL + `/api/admin/store_address/${id}`,
        headers: {
          "Content-Type": "multipart/form-data",
        },
        data: this.select_address
      }).then(() => {
        this.GET_ADDRESS({ limit: 100 });
      });
    },
  },
};
</script>

<style scoped>
.category_admin_views {
  margin-left: 80px;
  margin-right: 80px;
}

.table-responsive {
  background-color: #f8f8f8;
  border-radius: 10px;
  box-shadow: 0px 0px 9px -2px rgba(0, 0, 0, 0.2);
  padding: 0 10px 0 10px;
  margin-top: 20px;
}

tbody, td, tfoot, th, thead, tr  {
  border-style: none;
}

.btn {
  box-shadow: none;
}

.btn-group {
  gap: 10px;
}
</style>
