<template>
  <HeaderComponent :color="'black'" />
  <div class="container d-flex flex-column">
    <span class="fs-1 text-center cart-empty" :hidden="CART.length"
      >В корзине пусто</span
    >
    <div
      v-for="(product, index) in CART"
      :key="product"
      class="cart-product position-relative d-flex border border-1 align-items-center"
    >
      <button class="remove-btn" @click="REMOVE_FROM_CART(index)">
        <svg
          height="30px"
          style="enable-background: new 0 0 512 512"
          version="1.1"
          viewBox="0 0 512 512"
          width="30px"
          xml:space="preserve"
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink"
        >
          <g class="st2" id="cross">
            <g class="st0">
              <line class="st1" x1="112.5" x2="401" y1="112.5" y2="401" />
              <line class="st1" x1="401" x2="112.5" y1="112.5" y2="401" />
            </g>
          </g>
          <g id="cross_copy">
            <path
              d="M268.064,256.75l138.593-138.593c3.124-3.124,3.124-8.189,0-11.313c-3.125-3.124-8.189-3.124-11.314,0L256.75,245.436   L118.157,106.843c-3.124-3.124-8.189-3.124-11.313,0c-3.125,3.124-3.125,8.189,0,11.313L245.436,256.75L106.843,395.343   c-3.125,3.125-3.125,8.189,0,11.314c1.562,1.562,3.609,2.343,5.657,2.343s4.095-0.781,5.657-2.343L256.75,268.064l138.593,138.593   c1.563,1.562,3.609,2.343,5.657,2.343s4.095-0.781,5.657-2.343c3.124-3.125,3.124-8.189,0-11.314L268.064,256.75z"
            />
          </g>
        </svg>
      </button>
      <div class="cart-wrapper-content">
        <img :src="SERVER_URL + bonusUri + product.img" alt="" />
        <div class="count-price-wrapper">
          <span class="mb-auto mt-4 ms-5 name-prod">{{ product.title }}</span>
          <div class="d-flex ms-auto">
            <button
              class="count-btn count-btn-minus"
              @click="changeCount(product, -1)"
            >
              <span>-</span>
            </button>
            <input type="text" class="count-input" :value="product.count" />
            <button
              class="count-btn count-btn-plus"
              @click="changeCount(product, 1)"
            >
              <span>+</span>
            </button>
          </div>
          <span class="d-flex flex-column ms-5 text-center fs-5"
            >{{
              product.priceAndVolume[product.volume].price * product.count
            }}
            р</span
          >
        </div>
      </div>
    </div>
    <div class="wrapper-btn">
      <button
        class="submit-btn"
        :hidden="!CART.length"
        @click="activeModalWindow"
      >
        Оформить заказ
      </button>
    </div>
  </div>
  <div
    class="modal-window"
    :class="{ active_shadow: active === true }"
    @click="deactiveModalWindow"
  >
    <div
      class="modal-window_popup"
      @click.stop
      :class="{ active_modal_window: activeWindow === true }"
    >
      <div class="wrapper-svg">
        <h2>Оформление товара</h2>
        <svg
          height="40px"
          style="enable-background: new 0 0 512 512"
          version="1.1"
          viewBox="0 0 512 512"
          width="30px"
          xml:space="preserve"
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink"
          @click="deactiveModalWindow"
        >
          <g class="st2" id="cross">
            <g class="st0">
              <line class="st1" x1="112.5" x2="401" y1="112.5" y2="401" />
              <line class="st1" x1="401" x2="112.5" y1="112.5" y2="401" />
            </g>
          </g>
          <g id="cross_copy">
            <path
              d="M268.064,256.75l138.593-138.593c3.124-3.124,3.124-8.189,0-11.313c-3.125-3.124-8.189-3.124-11.314,0L256.75,245.436   L118.157,106.843c-3.124-3.124-8.189-3.124-11.313,0c-3.125,3.124-3.125,8.189,0,11.313L245.436,256.75L106.843,395.343   c-3.125,3.125-3.125,8.189,0,11.314c1.562,1.562,3.609,2.343,5.657,2.343s4.095-0.781,5.657-2.343L256.75,268.064l138.593,138.593   c1.563,1.562,3.609,2.343,5.657,2.343s4.095-0.781,5.657-2.343c3.124-3.125,3.124-8.189,0-11.314L268.064,256.75z"
            />
          </g>
        </svg>
      </div>
      <form class="modal-window_popup__form mt-5" @submit="submit">
        <div class="modal-window_popup__form__div">
          <div class="wrapper-first-column">
            <div class="modal-window_popup__input">
              <span>Имя получателя</span>
              <input
                type="text"
                v-model="form.name"
                required
                placeholder="Введите имя получателя"
              />
            </div>
            <div class="modal-window_popup__input">
              <span>Мобильный телефон</span>
              <input
                type="text"
                required
                placeholder="Введите номер телефона"
                v-model="form.phone_number"
              />
            </div>
          </div>
          <div class="wrapper-second-column">
            <div class="modal-window_popup__input">
              <span>E-mail</span>
              <input
                type="email"
                required
                placeholder="Введите e-mail"
                v-model="form.email"
              />
            </div>
            <div class="modal-window_popup__input">
              <span>Адрес доставки</span>
              <input
                type="text"
                required
                placeholder="Город,улица,номер дома"
                v-model="form.city"
              />
            </div>
          </div>
        </div>
        <button class="submit submit-form">Отправить</button>
      </form>
    </div>
  </div>
  <FooterComponent />
</template>

<script>
import HeaderComponent from "@/components/client/HeaderComponent.vue";
import FooterComponent from "@/components/client/FooterComponent.vue";
import { mapActions, mapGetters } from "vuex";
import axios from "axios";
export default {
  name: "CartPage",
  components: { HeaderComponent, FooterComponent },
  data() {
    return {
      SERVER_URL: process.env.VUE_APP_SERVER_URL,
      mode: process.env.NODE_ENV,
      bonusUri: process.env.NODE_ENV == "production" ? "/static/" : "/",
      active: false,
      activeWindow: false,
      form: {
        name: "",
        phone_number: 0,
        email: "",
        city: "",
      },
    };
  },
  computed: {
    ...mapGetters(["CART"]),
  },
  created() {
    console.log(this.CART);
  },
  methods: {
    changeCount(product, add) {
      product.count = Math.max(product.count + add, 1);
      localStorage.setItem(
        "cart",
        JSON.stringify(
          this.CART.map((item) => ({
            id: item.id,
            volume: item.volume,
            count: item.count,
          }))
        )
      );
    },
    activeModalWindow() {
      if (!this.CART.length) return;
      this.active = true;
      this.activeWindow = true;
      document.body.style.overflow = "hidden";
    },
    deactiveModalWindow() {
      this.active = false;
      this.activeWindow = false;
      document.body.style.overflow = "inherit";
    },
    plusBtn() {
      this.form.quantity_of_goods++;
    },
    minusBtn() {
      if (this.form.quantity_of_goods > 1) {
        this.form.quantity_of_goods--;
      } else {
        return;
      }
    },
    submit(event) {
      event.preventDefault();
      axios({
        method: "POST",
        url: process.env.VUE_APP_SERVER_URL + "/api/admin/user_client",
        data: {
          ...this.form,
          products: this.CART.map((item) => ({
            id: item.id,
            volume: item.volume,
            count: item.count,
          })),
        },
      })
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        })
        .finally(() => {
          this.deactiveModalWindow();
          this.CLEAR_CART();
          this.$router.go();
        });
    },
    ...mapActions(["CLEAR_CART", "REMOVE_FROM_CART"]),
  },
};
</script>

<style scoped>
