  }
  .wrapper-btn {
    justify-content: center;
    margin-right: 0;
  }

  .count-price-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .product-description {
    max-width: 480px;
    gap: 12px;
    width: 100%;
  }
  .product-img img {
    max-width: 480px;
    width: 102%;
  }

  .modal-window_popup__input span,
  p {
    font-size: 12px;
  }
  .modal-window_popup__input input {
