<template>
  <HeaderComponent :color="'black'" />
  <div class="wrapper-products container">
    <div
      class="product-item"
      v-for="(item, index) in PRODUCTS.rows"
      :key="index"
    >
      <div class="product-item_img">
        <img :src="SERVER_URL + bonusUri + item.img" alt="" />
      </div>
      <div class="product-item_price">
        <span
          >{{ item.priceAndVolume[0]?.price }} р ({{
            item.priceAndVolume[0]?.volume
          }}мл)</span
        >
      </div>
      <div class="product-item_title">
        <h2>{{ item.title }}</h2>
      </div>
      <div class="product-item_description">
        <span> {{ item.description }} </span>
      </div>
      <div class="product-item_btn">
        <router-link :to="{ path: '/about_product/' + item.id }"
          ><button>Купить</button></router-link
        >
      </div>
    </div>
  </div>
  <PaginationComponent
    :links="
      Array(Math.ceil((PRODUCTS.count || 1) / 12))
        .fill(this.$route.path + '?page=')
        .map((val, i) => (i == 0 ? this.$route.path : val + (i + 1)))
    "
  />
  <FooterComponent />
</template>

<script>
import HeaderComponent from "@/components/client/HeaderComponent.vue";
import FooterComponent from "@/components/client/FooterComponent.vue";
import PaginationComponent from "@/components/client/PaginationComponent.vue";
import { mapActions, mapGetters } from "vuex";

export default {
  name: "ProductsPage",
  components: { HeaderComponent, FooterComponent, PaginationComponent },
  data() {
    return {
      SERVER_URL: process.env.VUE_APP_SERVER_URL,
      mode: process.env.NODE_ENV,
      bonusUri: process.env.NODE_ENV == "production" ? "/static/" : "/",
    };
  },
  computed: {
    ...mapGetters(["PRODUCTS"]),
  },
  created() {
    this.$watch(
      () => this.$route.query,
      () => {
        this.GET_PRODUCTS({
          collection: this.$route.params.collection,
          page: this.$route.query.page,
          limit: 12,
        });
      },
      // fetch the data when the view is created and the data is
      // already being observed
      { immediate: true }
    );
  },
  methods: {
    ...mapActions(["GET_PRODUCTS"]),
  },
};
</script>

<style scoped>
.product-item {
  max-width: 257px;
  max-height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 5px;
}
.product-item_img img {
  height: 266px;
  width: 257px;
  object-fit: cover;
}
.product-item_title h2 {
  font-size: 25px;
}
.product-item_description {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.product-item_description span {
  font-size: 13px;
}
.product-item_btn button {
  width: 100%;
  background: black;
  color: #fff;
  padding: 7px 0;
}
.wrapper-products {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin-top: 15px;
  margin-bottom: 30px;
}
@media (max-width: 480px) {
  .wrapper-products {
    grid-template-columns: 256px;
    justify-content: center;
  }
}
</style>
